'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'

interface Token {
  mint: string
  name: string
  symbol: string
  decimals: number
  supply: number
  description?: string
  tokenAccount: string
  mintAuthority: string
  freezeAuthority: string | null
}

interface Transaction {
  id: string
  signature: string
  fromTokenAccount: string
  toTokenAccount: string
  amount: number
  mint: string
  status: string
  createdAt: string
}

export default function Web3Demo() {
  const [tokens, setTokens] = useState<Token[]>([])
  const [transactions, setTransactions] = useState<Transaction[]>([])
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState('')

  // –§–æ—Ä–º–∞ —Å–æ–∑–¥–∞–Ω–∏—è —Ç–æ–∫–µ–Ω–∞
  const [tokenForm, setTokenForm] = useState({
    name: '',
    symbol: '',
    decimals: 9,
    supply: 1000000,
    description: ''
  })

  // –§–æ—Ä–º–∞ –ø–µ—Ä–µ–≤–æ–¥–∞ —Ç–æ–∫–µ–Ω–æ–≤
  const [transferForm, setTransferForm] = useState({
    fromTokenAccount: '',
    toTokenAccount: '',
    amount: 0,
    mint: ''
  })

  // –§–æ—Ä–º–∞ –ø—Ä–æ–≤–µ—Ä–∫–∏ –±–∞–ª–∞–Ω—Å–∞
  const [balanceForm, setBalanceForm] = useState({
    tokenAccount: ''
  })

  const [balance, setBalance] = useState<any>(null)

  // –ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö
  const loadData = async () => {
    try {
      setLoading(true)
      const [tokensRes, transactionsRes] = await Promise.all([
        fetch('/api/web3/tokens'),
        fetch('/api/web3/transactions')
      ])
      
      const tokensData = await tokensRes.json()
      const transactionsData = await transactionsRes.json()
      
      setTokens(tokensData.tokens || [])
      setTransactions(transactionsData.transactions || [])
    } catch (error) {
      console.error('Error loading data:', error)
      setMessage('–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –¥–∞–Ω–Ω—ã—Ö')
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    loadData()
  }, [])

  // –°–æ–∑–¥–∞–Ω–∏–µ —Ç–æ–∫–µ–Ω–∞
  const createToken = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      setLoading(true)
      const response = await fetch('/api/web3/create-token', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(tokenForm)
      })
      
      const data = await response.json()
      if (response.ok) {
        setMessage('–¢–æ–∫–µ–Ω —É—Å–ø–µ—à–Ω–æ —Å–æ–∑–¥–∞–Ω!')
        setTokenForm({ name: '', symbol: '', decimals: 9, supply: 1000000, description: '' })
        loadData()
      } else {
        setMessage(`–û—à–∏–±–∫–∞: ${data.error}`)
      }
    } catch (error) {
      console.error('Error creating token:', error)
      setMessage('–û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è —Ç–æ–∫–µ–Ω–∞')
    } finally {
      setLoading(false)
    }
  }

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –±–∞–ª–∞–Ω—Å–∞
  const checkBalance = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      setLoading(true)
      const response = await fetch('/api/web3/get-balance', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(balanceForm)
      })
      
      const data = await response.json()
      if (response.ok) {
        setBalance(data)
        setMessage('–ë–∞–ª–∞–Ω—Å –ø–æ–ª—É—á–µ–Ω!')
      } else {
        setMessage(`–û—à–∏–±–∫–∞: ${data.error}`)
        setBalance(null)
      }
    } catch (error) {
      console.error('Error checking balance:', error)
      setMessage('–û—à–∏–±–∫–∞ –ø—Ä–æ–≤–µ—Ä–∫–∏ –±–∞–ª–∞–Ω—Å–∞')
    } finally {
      setLoading(false)
    }
  }

  // –ü–µ—Ä–µ–≤–æ–¥ —Ç–æ–∫–µ–Ω–æ–≤
  const transferTokens = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      setLoading(true)
      const response = await fetch('/api/web3/transfer', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(transferForm)
      })
      
      const data = await response.json()
      if (response.ok) {
        setMessage('–ü–µ—Ä–µ–≤–æ–¥ –≤—ã–ø–æ–ª–Ω–µ–Ω —É—Å–ø–µ—à–Ω–æ!')
        setTransferForm({ fromTokenAccount: '', toTokenAccount: '', amount: 0, mint: '' })
        loadData()
      } else {
        setMessage(`–û—à–∏–±–∫–∞: ${data.error}`)
      }
    } catch (error) {
      console.error('Error transferring tokens:', error)
      setMessage('–û—à–∏–±–∫–∞ –ø–µ—Ä–µ–≤–æ–¥–∞ —Ç–æ–∫–µ–Ω–æ–≤')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="container mx-auto p-6 space-y-6">
      <div className="text-center">
        <h1 className="text-3xl font-bold mb-2">Web3 –î–µ–º–æ (–°–∏–º—É–ª—è—Ü–∏—è)</h1>
        <p className="text-muted-foreground">
          –î–µ–º–æ–Ω—Å—Ç—Ä–∞—Ü–∏—è —Ñ—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω–æ—Å—Ç–∏ —Å–æ–∑–¥–∞–Ω–∏—è —Ç–æ–∫–µ–Ω–æ–≤, –ø–µ—Ä–µ–≤–æ–¥–æ–≤ –∏ –ø—Ä–æ–≤–µ—Ä–∫–∏ –±–∞–ª–∞–Ω—Å–æ–≤
        </p>
      </div>

      {message && (
        <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg">
          <p className="text-blue-800">{message}</p>
        </div>
      )}

      <Tabs defaultValue="create" className="w-full">
        <TabsList className="grid w-full grid-cols-4">
          <TabsTrigger value="create">–°–æ–∑–¥–∞—Ç—å —Ç–æ–∫–µ–Ω</TabsTrigger>
          <TabsTrigger value="balance">–ü—Ä–æ–≤–µ—Ä–∏—Ç—å –±–∞–ª–∞–Ω—Å</TabsTrigger>
          <TabsTrigger value="transfer">–ü–µ—Ä–µ–≤–µ—Å—Ç–∏ —Ç–æ–∫–µ–Ω—ã</TabsTrigger>
          <TabsTrigger value="history">–ò—Å—Ç–æ—Ä–∏—è</TabsTrigger>
        </TabsList>

        <TabsContent value="create">
          <Card>
            <CardHeader>
              <CardTitle>–°–æ–∑–¥–∞–Ω–∏–µ —Ç–æ–∫–µ–Ω–∞</CardTitle>
              <CardDescription>
                –°–æ–∑–¥–∞–π—Ç–µ –Ω–æ–≤—ã–π —Ç–æ–∫–µ–Ω –≤ —Å–∏–º—É–ª—è—Ü–∏–∏ –±–ª–æ–∫—á–µ–π–Ω–∞
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={createToken} className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="name">–ù–∞–∑–≤–∞–Ω–∏–µ —Ç–æ–∫–µ–Ω–∞</Label>
                    <Input
                      id="name"
                      value={tokenForm.name}
                      onChange={(e) => setTokenForm({...tokenForm, name: e.target.value})}
                      placeholder="My Token"
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="symbol">–°–∏–º–≤–æ–ª</Label>
                    <Input
                      id="symbol"
                      value={tokenForm.symbol}
                      onChange={(e) => setTokenForm({...tokenForm, symbol: e.target.value})}
                      placeholder="MTK"
                      required
                    />
                  </div>
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="decimals">–î–µ—Å—è—Ç–∏—á–Ω—ã–µ –∑–Ω–∞–∫–∏</Label>
                    <Input
                      id="decimals"
                      type="number"
                      value={tokenForm.decimals}
                      onChange={(e) => setTokenForm({...tokenForm, decimals: Number(e.target.value)})}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="supply">–û–±—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ</Label>
                    <Input
                      id="supply"
                      type="number"
                      value={tokenForm.supply}
                      onChange={(e) => setTokenForm({...tokenForm, supply: Number(e.target.value)})}
                      required
                    />
                  </div>
                </div>
                <div>
                  <Label htmlFor="description">–û–ø–∏—Å–∞–Ω–∏–µ</Label>
                  <Input
                    id="description"
                    value={tokenForm.description}
                    onChange={(e) => setTokenForm({...tokenForm, description: e.target.value})}
                    placeholder="–û–ø–∏—Å–∞–Ω–∏–µ —Ç–æ–∫–µ–Ω–∞"
                  />
                </div>
                <Button type="submit" disabled={loading}>
                  {loading ? '–°–æ–∑–¥–∞–Ω–∏–µ...' : '–°–æ–∑–¥–∞—Ç—å —Ç–æ–∫–µ–Ω'}
                </Button>
              </form>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="balance">
          <Card>
            <CardHeader>
              <CardTitle>–ü—Ä–æ–≤–µ—Ä–∫–∞ –±–∞–ª–∞–Ω—Å–∞</CardTitle>
              <CardDescription>
                –ü—Ä–æ–≤–µ—Ä—å—Ç–µ –±–∞–ª–∞–Ω—Å —Ç–æ–∫–µ–Ω –∞–∫–∫–∞—É–Ω—Ç–∞
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={checkBalance} className="space-y-4">
                <div>
                  <Label htmlFor="tokenAccount">–ê–¥—Ä–µ—Å —Ç–æ–∫–µ–Ω –∞–∫–∫–∞—É–Ω—Ç–∞</Label>
                  <Input
                    id="tokenAccount"
                    value={balanceForm.tokenAccount}
                    onChange={(e) => setBalanceForm({...balanceForm, tokenAccount: e.target.value})}
                    placeholder="–í–≤–µ–¥–∏—Ç–µ –∞–¥—Ä–µ—Å —Ç–æ–∫–µ–Ω –∞–∫–∫–∞—É–Ω—Ç–∞"
                    required
                  />
                </div>
                <Button type="submit" disabled={loading}>
                  {loading ? '–ü—Ä–æ–≤–µ—Ä–∫–∞...' : '–ü—Ä–æ–≤–µ—Ä–∏—Ç—å –±–∞–ª–∞–Ω—Å'}
                </Button>
              </form>
              
              {balance && (
                <div className="mt-4 p-4 bg-green-50 border border-green-200 rounded-lg">
                  <h3 className="font-semibold text-green-800">–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –±–∞–ª–∞–Ω—Å–µ:</h3>
                  <p><strong>–ë–∞–ª–∞–Ω—Å:</strong> {balance.balance}</p>
                  <p><strong>Mint:</strong> {balance.mint}</p>
                  <p><strong>–í–ª–∞–¥–µ–ª–µ—Ü:</strong> {balance.owner}</p>
                  <p><strong>–î–µ—Å—è—Ç–∏—á–Ω—ã–µ –∑–Ω–∞–∫–∏:</strong> {balance.decimals}</p>
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="transfer">
          <Card>
            <CardHeader>
              <CardTitle>–ü–µ—Ä–µ–≤–æ–¥ —Ç–æ–∫–µ–Ω–æ–≤</CardTitle>
              <CardDescription>
                –ü–µ—Ä–µ–≤–µ–¥–∏—Ç–µ —Ç–æ–∫–µ–Ω—ã –º–µ–∂–¥—É –∞–∫–∫–∞—É–Ω—Ç–∞–º–∏
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={transferTokens} className="space-y-4">
                <div>
                  <Label htmlFor="fromAccount">–û—Ç –∞–∫–∫–∞—É–Ω—Ç–∞</Label>
                  <Input
                    id="fromAccount"
                    value={transferForm.fromTokenAccount}
                    onChange={(e) => setTransferForm({...transferForm, fromTokenAccount: e.target.value})}
                    placeholder="–ê–¥—Ä–µ—Å –æ—Ç–ø—Ä–∞–≤–∏—Ç–µ–ª—è"
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="toAccount">–ö –∞–∫–∫–∞—É–Ω—Ç—É</Label>
                  <Input
                    id="toAccount"
                    value={transferForm.toTokenAccount}
                    onChange={(e) => setTransferForm({...transferForm, toTokenAccount: e.target.value})}
                    placeholder="–ê–¥—Ä–µ—Å –ø–æ–ª—É—á–∞—Ç–µ–ª—è"
                    required
                  />
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="amount">–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ</Label>
                    <Input
                      id="amount"
                      type="number"
                      value={transferForm.amount}
                      onChange={(e) => setTransferForm({...transferForm, amount: Number(e.target.value)})}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="mint">Mint –∞–¥—Ä–µ—Å</Label>
                    <Input
                      id="mint"
                      value={transferForm.mint}
                      onChange={(e) => setTransferForm({...transferForm, mint: e.target.value})}
                      placeholder="Mint –∞–¥—Ä–µ—Å —Ç–æ–∫–µ–Ω–∞"
                      required
                    />
                  </div>
                </div>
                <Button type="submit" disabled={loading}>
                  {loading ? '–ü–µ—Ä–µ–≤–æ–¥...' : '–ü–µ—Ä–µ–≤–µ—Å—Ç–∏ —Ç–æ–∫–µ–Ω—ã'}
                </Button>
              </form>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="history">
          <div className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>–°–æ–∑–¥–∞–Ω–Ω—ã–µ —Ç–æ–∫–µ–Ω—ã</CardTitle>
                <CardDescription>
                  –°–ø–∏—Å–æ–∫ –≤—Å–µ—Ö —Å–æ–∑–¥–∞–Ω–Ω—ã—Ö —Ç–æ–∫–µ–Ω–æ–≤
                </CardDescription>
              </CardHeader>
              <CardContent>
                {tokens.length === 0 ? (
                  <p className="text-muted-foreground">–¢–æ–∫–µ–Ω—ã –Ω–µ –Ω–∞–π–¥–µ–Ω—ã</p>
                ) : (
                  <div className="space-y-2">
                    {tokens.map((token, index) => (
                      <div key={index} className="p-3 border rounded-lg">
                        <div className="flex justify-between items-start">
                          <div>
                            <h3 className="font-semibold">{token.name} ({token.symbol})</h3>
                            <p className="text-sm text-muted-foreground">{token.description}</p>
                            <p className="text-xs text-muted-foreground">
                              Supply: {token.supply} | Decimals: {token.decimals}
                            </p>
                          </div>
                        </div>
                        <div className="mt-2 text-xs text-muted-foreground">
                          <p><strong>Mint:</strong> {token.mint}</p>
                          <p><strong>Token Account:</strong> {token.tokenAccount}</p>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>–ò—Å—Ç–æ—Ä–∏—è —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–π</CardTitle>
                <CardDescription>
                  –í—Å–µ –≤—ã–ø–æ–ª–Ω–µ–Ω–Ω—ã–µ –ø–µ—Ä–µ–≤–æ–¥—ã —Ç–æ–∫–µ–Ω–æ–≤
                </CardDescription>
              </CardHeader>
              <CardContent>
                {transactions.length === 0 ? (
                  <p className="text-muted-foreground">–¢—Ä–∞–Ω–∑–∞–∫—Ü–∏–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω—ã</p>
                ) : (
                  <div className="space-y-2">
                    {transactions.map((tx, index) => (
                      <div key={index} className="p-3 border rounded-lg">
                        <div className="flex justify-between items-start">
                          <div>
                            <h3 className="font-semibold">–ü–µ—Ä–µ–≤–æ–¥ {tx.amount} —Ç–æ–∫–µ–Ω–æ–≤</h3>
                            <p className="text-sm text-muted-foreground">
                              –û—Ç: {tx.fromTokenAccount.slice(0, 8)}...{tx.fromTokenAccount.slice(-8)}
                            </p>
                            <p className="text-sm text-muted-foreground">
                              –ö: {tx.toTokenAccount.slice(0, 8)}...{tx.toTokenAccount.slice(-8)}
                            </p>
                          </div>
                          <span className={`px-2 py-1 rounded text-xs ${
                            tx.status === 'confirmed' ? 'bg-green-100 text-green-800' : 'bg-yellow-100 text-yellow-800'
                          }`}>
                            {tx.status}
                          </span>
                        </div>
                        <div className="mt-2 text-xs text-muted-foreground">
                          <p><strong>Signature:</strong> {tx.signature.slice(0, 16)}...</p>
                          <p><strong>Mint:</strong> {tx.mint}</p>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </TabsContent>
      </Tabs>
    </div>
  )
}
